@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@250;300&display=swap);
.container {
    position: relative;
    margin-left: 0;
    padding-left: 0;
    max-width: 10px;
}

.navbar {
    background-color: #263238;
    text-align: left;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;

}

.navbar-nav .nav-link {
    color: #f5f5f5 !important
}

.navbar-nav .nav-item {
    position: relative;
    margin-right: 20px;
}

.navbar-nav .nav-item a:hover {
    background-color: #276678;
}

.toggle-icon {
    color: #f5f5f5 !important;
}

.navbar-toggler {
    padding: 0.25rem 1.5rem !important;
    position: relative !important;
    background-color: transparent !important;
    border: none transparent !important;
    outline: 0 !important;
}
.navbar-toggler:focus,
.navbar-toggler:active {
    outline: 0 !important;
    box-shadow: none;
}

span {
    margin-left: 1rem;
}

.tags {
    margin: 0.5rem 0 0.5rem 0;
}

.tag {
    font-size: 11px !important;
    text-align: left !important;
    padding: 5px 8px 5px 8px !important;
    vertical-align: baseline;
    font-weight: lighter !important;
    margin: 0 6px 6px 2px;
    letter-spacing: 1px;
}


.title {
    font-size: 15em;
    text-align: center;
}

h3 {
    padding-top: 0.25em;
}

.vertical-timeline-element-title {
    text-align: center;
    margin-bottom: 0.7rem;
    font-size: 30px !important;
}

.vertical-timeline-element-content {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
    padding: 2em 3em !important;
    background: #F6F6F6;
}


.vertical-timeline-element-description{
    margin-bottom: 1rem;
    font-size: 18px !important;
}

.vertical-timeline-element-icon {
    background: #263238;
    color: #fff;
    text-align: left;
}

.vertical-timeline-element-content-arrow {
    border-right: 7px solid  #F6F6F6;
    border-right: 7px solid  #F6F6F6;
}

.home-header h1 {
    color: #f5f5f5;
    font-size: 3em;
}

.main-name {
    color: #F6F2D4;
}

.Typewriter__wrapper {
    font-size: 2.8em;
    color: #f5f5f5;
    font-weight: 600;

}
.Typewriter__cursor {
    font-size: 2.4em;
    color: #f5f5f5;
}

.about {
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-card {
    margin-left: auto;
    margin-right: auto;
}

.card-header .card-body {
    margin: auto;
    display: flex;
    justify-content: center;

}

.wrapper {
    margin: 2rem;
    display: grid;
    grid-gap: 4rem;
    gap: 4rem;
    grid-template-columns: 1fr;
    justify-content: center;
}


.card {
    background-color: #F6F6F6 !important;
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
    overflow: hidden;
    display: flex;
    transition: 0.5s;
    border: none;
    z-index: 1;
}

.card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}

.card__img {
    width: 100%;
    height: 11.8rem;
    object-fit: cover;
}

.card__body {
    margin: 2rem 1rem 2rem 1rem;
    flex-grow: 1;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.card__title {
    line-height: 1.8rem;
    margin-bottom: 0.8rem;
    text-align: center;
}

.card__description {
    line-height: 1.5rem;
    padding-bottom: 5px;
}

@media screen and (min-width: 500px) {
    .wrapper {
        grid-template-columns: repeat(auto-fit, minmax(20rem, 25rem));
    }

    .card {
        flex-direction: column;
        text-align: left;
    }

    .card__img {
        width: 100%;
        height: 11.8rem;
    }
}


.card__line {
    border-top:1px dotted #000;
    background:#263238;
    width: 90%;
    margin: auto;
    position: absolute;
    bottom: 2.7rem;
}

.card__btn {
    transition: 0.2s;
    color: #263238;
    padding-right: 1rem;
}

.card__btn:hover {
    color: #276678;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.card__footer {
    display: inline;
    padding-right: 20px;
    padding-left: 0;
    list-style: "-";
    justify-content: space-between;
    position: absolute;
    bottom: 0;
}

.card__footer li {
    display: inline-flex;
}





.sec-skills-icons,
.skills-icons {
    bottom: 0 !important;
    color: #f5f5f5;
    list-style-type: none;
    horiz-align: center;
    vertical-align: center;
    align-items: center;
    display: inline-block;
    flex-direction: row;
    justify-content: space-around;
    padding: 2rem;
    text-align: center;
    background-color: #263238;
    margin: 2.5rem;
    border-radius: 25px;
}

.sec-skills-icons {
    font-size: 1em;
    text-align: center;
}

.skills-div {
    align-items: center;
    text-align: center;
}


.main-skills-h1,
.complementary-skills-h1 {
    width: 100%;
    overflow: hidden;
    vertical-align:middle;
    color: #f5f5f5;
    background-color: #66806A;
    padding: 5px;
}

.skills-icons-ul {
    padding: 10px 0 10px 0;
}

.pskills-icons {
    margin: 0;
}

.footer {
    background-color: #263238;
    width: 100%;
    bottom: 0 !important;
    padding-top: 10px;
    padding-bottom: 10px;
    justify-content: space-between;
    margin-top: 30px;
}

.footer-body {
    text-align: center !important;
    bottom: 0;
    overflow: hidden;
    position: relative;
    color: #f5f5f5;
}

.social-icons {
    display: inline-block;
    padding-right: 20px;
    padding-left: 20px;
    color: #f5f5f5;
}


body {
    margin: 0;
    font-family: "Raleway", serif !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #99A799;
}

